@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

a {
  color: inherit;
}

body {
  color: #333;
  background-color: #d6d6d6;
}

.header {
  text-align: center;
  margin: 80px auto;
}

.check {
  max-width: 640px;
  width: calc(100% - 30px);
  margin: 0px auto;
}

.check form {
  position: relative;
}

.check form input[type="text"] {
  width: 100%;
  height: 50px;
  border: 1px solid #999;
  outline: none;
  padding: 0 20px;
}

.check form button {
  width: 70px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  background-color: transparent;
  border: 1px solid #999;
}

.check form button img {
  width: 100%;
  height: 35px;
  object-fit: contain;
}

.check .check_list {
  margin-top: 50px;
  border-top: 1px solid #999;

}

.check .check_list ul  {
  padding: 40px 0;
}

.check .check_list ul .item {
  border: 1px solid #999;
  background-color: slategray;
  color: white;
  margin-top: 10px;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.check .check_list ul .item button {
  width: 30px;
  height: 30px;
  background-color: white;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
}

.check .check_list ul .item button img {
  display: block;
  width: 100%;
  height: 10px;
  object-fit: contain;
}

.dateWrap {
  display: flex;
  gap: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}